.footer{
    width: 100%;
    padding: 2rem 0;
    background-color: rgba(19, 19, 19, 0.8);
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    justify-content: center;
    align-items: center;
}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location{
    display: flex;
    margin-bottom: 0.8rem;
}

.location p{
    line-height: 30px;
}


.rigth{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}


.social{
    margin-top: 1rem;
}

h4{
    font-size: 1rem;
    padding-bottom: 0.7rem;
}

p{
    font-size: 1rem;
}


@media screen and (max-width: 640px){

    .footer-container{
        
        grid-template-columns: 1fr;
        
    }

    .left{
        margin-left: 1rem;
    }

    h4{
        margin-left: 2rem;
    }

    .social{
        margin-left: 2rem;
    }

}